@import url(https://use.fontawesome.com/releases/v5.11.2/css/all.css);
@import url(https://use.fontawesome.com/releases/v5.11.2/css/v4-shims.css);

.results-total {
  margin-top: 1.2em;
}

.results-card-header {
  font-size: 0.9em;
}

.result-list-row {
  font-size: 0.85em;
  padding-top: 0.6em;
  padding-bottom: 0.6em;
}

.result-number {
  font-size: 0.9em;
  font-weight: 300;
  padding: 3px;
}

.result-sport {
  padding: 3px;
  text-align: left;
}

.fas {
  color: #00bf8a;
  padding: 0.2em;
  font-size: 1.2em;
  font-weight: 300;
}

.menu-header {
  background-color: #212529;
}

.menu-sport {
  text-align: left;
  font-size: 1.2em;
  font-weight: 300;
}
.navbar-top {
  padding: 1em 1em 1em 1em; 
  text-align: left;
  background-color: #212529;
  z-index: 100;
  overflow: hidden;
  position: fixed;
  top: 0px;
  width: 100%;
}

.navbar {  
  padding: 0.5em 1.5em 0.5em 1.5em;
  background-color: #212529;
  position: fixed;
  bottom: 0;
  width: 100%;
  font-weight: 300;
  font-size: 0.8em;
  justify-content: center;
}


.nav-row {
  text-align: center;
  align-content: justify;
  margin: 0px;
  display: block;
}







.btn-circle {
  width: 80px;
  height: 80px;
  padding: 10px 10px;
  font-size: 1.3em;
  color: white;
  line-height: 1.33;
  border-radius: 50%;
  border: 1px solid #212529;
  text-align: center;
  margin: 10px;
}

.btn-change {
  background-color:#00499B;
  color: white;
}

.btn-save {
  background-color:#00bf8a;
  color: white;
  border: none;
}

.btn-pause {
  background-color:#B90C3D;
  color: white;
}

.btn-xxl {
  color:white;
  padding: 1rem 1.5rem;
  font-size: 1.5rem;
}
.react-calendar {
  background-color: #212529;
  color: white;
}

.react-calendar__navigation__label {
  color: white;
  font-weight: 600;
}

abbr {
  color: white;
}

.react-calendar__month-view__weekdays {
  text-transform: none;
}

.react-calendar__tile--active:enabled:hover {
    background-color: #00bf8a;
    border-radius: 50%;

}


body {
  background-color: #212529;
  color: white;
  margin-bottom: 3em;
  margin-top: 3em;
  text-align: center;
  
}
.container {
  margin-top: 2em;
}

.save {
  background-color:#00bf8a;
  color: white;
}

.image {
  color: white;
}

h1 {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  margin-top: 1.8em;
  font-weight: 200;
}


a {
  color: white;
}

a.attribution {
  font-size: 0.5em;
  font-weight: 300;
}

a:link {
  text-decoration: none;
}

a:active {
  color: white; 
}

a:hover {
  color: #00bf8a; 
}



@font-face {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  src: local('Gill Sans')
}


